import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import BoardListItem from "./BoardListItem";

const BoardListContainer = styled.div`
  padding-bottom: 2rem;
  & > a,
  & {
    text-decoration: none;
    color: white;
    & > div {
      display: flex;
      flex-wrap: wrap;
      background-color: #1a1a1a;
      justify-content: space-between;
      align-items: center;
      font-size: 0.8125rem;
      padding: 1rem 0;
      min-height: 3.5rem;

      &:not(:last-of-type) {
        border-bottom: 0.5px solid gray;
      }
      div.id {
        width: 10%;
        text-align: center;
      }
      div.title {
        width: 65%;
        margin-right: 3%;
        line-height: 1.3;
      }
      div.timestamp {
        width: 22%;
        text-align: center;
      }
      div.arrow {
        width: 7%;
        display: none;
        justify-content: flex-start;
        /* display: none; */
        img {
          width: 10px;
          height: 10px;
          fill: white;
        }
      }
      ${props =>
        props.expandable &&
        `
    div.id, div.timestamp {
      display: none;
    }
    div.title {
      width: 90%;
      padding: 0 3%;
      &::before {
        content: "Q. "
      }
    }
    div.arrow {
      display: flex;
    }
    `}
    }
  }
  div.expanded_body {
    color: #1a1a1a;
    padding: 0 3%;
    background-color: white;
  }
`;

const BoardList = ({ posts, expandable, url }) => {
  return (
    <BoardListContainer expandable={expandable}>
      {expandable
        ? posts.map(post => <BoardListItem post={post} />)
        : posts.map(post => (
            <NavLink to={`${url}/${post.id}`}>
              <BoardListItem post={post} />
            </NavLink>
          ))}
    </BoardListContainer>
  );
};

export default BoardList;
